import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {OwlOptions} from 'ngx-owl-carousel-o';
import {AuthService} from '../../../../services/auth.service';
import {Apollo} from 'apollo-angular';
import {MatDialog} from '@angular/material/dialog';
import {AddPartnerComponent} from './add-partner/add-partner.component';
import {removePartner} from '../homequeries';

@Component({
  selector: 'app-partners',
  templateUrl: './partners.component.html',
  styleUrls: ['./partners.component.scss']
})
export class PartnersComponent implements OnInit {
  @Input() partners: any[] = [];
  @Output() refreshNow: EventEmitter<any> = new EventEmitter<any>();
  admin = false;
  slidesStore = [
    {id: '1', src: 'assets/partners/1.png', alt: 'image', headline: 'partner1', desc: 'any info about the project'},
    {id: '2', src: 'assets/partners/2.png', alt: 'image', headline: 'partner1', desc: 'any info about the project'},
    {id: '3', src: 'assets/partners/3.png', alt: 'image', headline: 'partner1', desc: 'any info about the project'},
    {id: '4', src: 'assets/partners/4.png', alt: 'image', headline: 'partner1', desc: 'any info about the project'},
    {id: '5', src: 'assets/partners/5.png', alt: 'image', headline: 'partner1', desc: 'any info about the project'},
  ];
  customOptions: OwlOptions = {
    center: false,
    margin: 40,
    autoWidth: false,
    animateIn: true,
    loop: true,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    dots: false,
    navSpeed: 700,
    nav: false,
    slideBy: 1,
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 2
      },
      740: {
        items: 3
      },
      940: {
        items: 5
      },
      1024: {
        items: 5
      }
    },
  };

  constructor(private auth: AuthService, public dialog: MatDialog, private apollo: Apollo) {
  }

  addpartner(id): void {
    this.openDialog(id);
  }

  deletePartner(id): void {
    const confirmme = confirm('are you sure you want to delete');
    if (confirmme) {
      this.apollo.mutate({
        mutation: removePartner,
        variables: {id: Number(id)}
      }).subscribe(() => {
        alert('deleted successfully');
        this.refreshNow.emit();
      });
    }
  }

  openDialog(id): void {
    const dialogRef1 = this.dialog.open(AddPartnerComponent);
    dialogRef1.componentInstance.id = null;
    dialogRef1.afterClosed().subscribe(() => {
      this.refreshNow.emit();
    });

  }

  ngOnInit(): void {
    this.auth.checkAdmin().subscribe(x => this.admin = x);

  }

}
