import {Component, OnInit} from '@angular/core';

import {HostListener, ElementRef} from '@angular/core';
import {Apollo} from 'apollo-angular';
import {homeQuery, navbarQuery} from './homequeries';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],

})
export class HomeComponent implements OnInit {
  state = 'hide';
  services: any;
  // products: any = [{
  //   title: 'prod1',
  //   shortDesc: 'lorem ipsium is easy a lorem ipsium is easy ' +
  //     'a lorem ipsium is easy a lorem ipsium is easy a lorem ipsium' +
  //     ' is easy a lorem ipsium is easy a lorem ipsium is easy a lorem' +
  //     ' ipsium is easy a lorem ipsium is easy a lorem ipsium is easy ' +
  //     'a lorem ipsium is easy ' +
  //     'a lorem ipsium is easy a lorem ipsium is easy a lorem' +
  //     ' ipsium is easy a lorem ipsium is easy a ',
  //   id: 2,
  //
  // }];
  products: any = [];
  loading: any;
  partners: any;

  constructor(public el: ElementRef, private apollo: Apollo) {
    this.get();
  }

  get(): void {
    this.apollo
      .watchQuery({
        query: homeQuery,
        fetchPolicy: 'network-only'
      })
      .valueChanges.subscribe((result: any) => {
      this.services = result?.data?.findManyParentServices?.entities;
      this.products = result?.data?.findManyProducts?.entities;

      this.partners = result?.data?.findManyPartners?.entities;
      this.loading = result.loading;
    });
  }

  ngOnInit(): void {

  }

  // @HostListener('window:scroll', ['$event'])
  // checkScroll() {
  //   const componentPosition = this.el.nativeElement.offsetTop;
  //   const scrollPosition = window.pageYOffset;
  //
  //   if (scrollPosition >= componentPosition) {
  //     this.state = 'show';
  //   } else {
  //     this.state = 'hide';
  //   }
  //
  // }
}
