import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {HomeComponent} from './home/home.component';
import {RouterModule, Routes} from '@angular/router';
import {SharedModule} from '../../shared/shared.module';
import {LandingComponent} from './home/landing/landing.component';
import {FeaturedProductsComponent} from './home/featured-products/featured-products.component';
import {PricingBannerComponent} from './home/pricing-banner/pricing-banner.component';
import {ServicesComponent} from './home/services/services.component';
import {PartnersComponent} from './home/partners/partners.component';
import {AboutUsComponent} from './home/about-us/about-us.component';
import {CarouselModule} from 'ngx-owl-carousel-o';
import {LottieModule} from 'ngx-lottie';
import {playerFactory} from '../pricing/pricing.module';
import {TranslateModule} from '@ngx-translate/core';
import { AddPartnerComponent } from './home/partners/add-partner/add-partner.component';


const appRoutes: Routes= [
  {
    path: '',
    component: HomeComponent
  }
];

@NgModule({
  declarations: [HomeComponent, LandingComponent, FeaturedProductsComponent,
    PricingBannerComponent, ServicesComponent, PartnersComponent, AboutUsComponent, AddPartnerComponent],
  imports: [
    CommonModule, SharedModule,
    RouterModule.forChild(appRoutes),
    LottieModule.forRoot({player: playerFactory})

  ]
})
export class HomeModule {
}
