import {Component, OnInit} from '@angular/core';
import {onePartner, oneProdQuery} from '../../homequeries';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Apollo} from 'apollo-angular';
import {createPartner, createProduct, updateProduct} from '../../homemutation';
import {Router} from '@angular/router';
import {MatDialogRef} from '@angular/material/dialog';

@Component({
  selector: 'app-add-partner',
  templateUrl: './add-partner.component.html',
  styleUrls: ['./add-partner.component.scss']
})
export class AddPartnerComponent implements OnInit {
  id: number;
  partnersForm: FormGroup;
  partner: any;
  loading = true;
  icon: any;
  image: any;
  toSend: any;
  toUpdate: any;
  lang = 'en';

  constructor(private apollo: Apollo, private fb: FormBuilder, private router: Router,
              public dialogRef: MatDialogRef<AddPartnerComponent>,
  ) {
  }

  ngOnInit(): void {
    if ((this.id != null) && (this.id !== undefined)) {
      this.get();
    } else {
      this.createNullForm();
    }
  }

  get(): void {
    this.apollo.watchQuery({
      query: onePartner,
      fetchPolicy: 'network-only',
      variables: {id: Number(this.id)}
    }).valueChanges.subscribe((result: any) => {
      this.loading = false;
      this.partner = result?.data?.findOnePartner;
      this.createExistingForm();
    });
  }

  createExistingForm(): void {
    this.partnersForm = this.fb.group({
      id: Number(this.id),
      onHome: [this.partner.onHome],
      isActive: [this.partner.isActive],
      title: this.fb.group({
        [this.lang]: [this.partner.title, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
      desc: this.fb.group({
        [this.lang]: [this.partner.desc, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
    });
  }

  createNullForm(): void {
    this.partnersForm = this.fb.group({
      onHome: [''],
      isActive: [''],
      title: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      }),
      desc: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      })
    });
  }

  send(): void {

    this.apollo.mutate({
      mutation: createPartner,
      variables: this.toSend,
      context: {
        useMultipart: true
      }
    }).subscribe(response => {
      if (response.data) {
        alert('Partner created successfully');
        this.close();

      }
    }, error => alert(error));
  }

  update(): void {

  }

  submit(): void {
    this.toSend = this.partnersForm.value;
    this.toSend.image = this.image;
    this.id ? this.update() : this.send();
  }

  handleFileInput(type, e): void {
    this.image = e.target.files[0];

  }
  close() {
    this.dialogRef.close();

  }
}
